<template>
  <div class="events">
    <v-dialog v-model="addGroupDialog" max-width="400px" overlay-opacity=".8" persistent>
        <v-card elevation="16" outlined >
          <v-form ref="addGroup" lazy-validation >
            <v-card-title class="headline justify-center mb-2"> Add Event Group</v-card-title>
            <v-card-subtitle class="font-italic">You must add an event to this group for it to be saved.</v-card-subtitle>
            <v-card-text>
              <v-text-field dense outlined v-model="addGroupTextBox" label="Group Name" :rules="$rules.noSpaces.concat($rules.required)" hint="No Spaces" persistent-hint></v-text-field>
            </v-card-text>
            </v-form>
            <v-card-subtitle v-if="addGroupWarning" class="font-italic red--text">{{addGroupWarning}}</v-card-subtitle>
            <v-card-actions>
                <v-spacer></v-spacer>                           
                <v-btn color="primary" text dark class="" @click="cancelAddGroup">Cancel</v-btn>
                <v-btn color="primary"   dark class="" @click="addGroup">Add</v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-dialog>
    <v-card class="pa-2" outlined> 
     
    <v-tabs horizontal v-model="selectedTab" show-arrows  >

      <v-tab  v-for="(group, key) in groups" :key="key">
        <v-icon small left v-if="group == 'default'">mdi-lock</v-icon> 
        <!-- <v-icon small left v-else>mdi-access-point</v-icon> -->
        {{group}}
       </v-tab>

      <v-spacer></v-spacer>
      <v-btn text color="primary" class="ma-2" @click="addGroupDialog=true"><v-icon small>{{$icon.mdiPlus}}</v-icon>group</v-btn>
      <v-btn icon color="primary" class="ma-2" @click="showInfo=true" ><v-icon >{{$icon.mdiInformationOutline}}</v-icon></v-btn>

      <v-tab-item v-for="(group, key) in groups" :key="key" class="mb-4">
        <div class ="  mt-4 " >
        <v-card  v-for="(type, key) in eventTypes" :key="key" outlined>
          <v-expansion-panels focusable :accordion="$vuetify.breakpoint.mobile" :inset="!$vuetify.breakpoint.mobile">  
            <v-expansion-panel>
              <v-expansion-panel-header>{{type}} Events List</v-expansion-panel-header>
              <v-expansion-panel-content>
          <v-card-text :class="['', { 'isMobile' : $vuetify.breakpoint.mobile }]">
             <EventsTable :activeTab="selectedTabName" :tableTitle="type" class="pa-0"/>
          </v-card-text>
            </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
        </v-card>
        <div class="d-flex justify-end pt-6">
          <v-btn text color="" class="redOverlay" width="" :disabled="isDefultGroup" @click="deleteGroupDialog_1 = true"><v-icon>{{$icon.mdiDelete}}</v-icon>DELETE GROUP</v-btn>
        </div>
        <v-dialog v-model="deleteGroupDialog_1" max-width="400px" overlay-opacity=".9" persistent >
                    <v-card outlined dark class="deleteDialog_1">
                        <v-card-title  class="headline">Delete Group - {{selectedTabName}}</v-card-title>
                        <v-card-text class="pt-2 body-2">This will permanently delete the group and all of the events! All email subscibers will be notified.</v-card-text>
                        <v-card-actions class="pb-4">
                            <v-spacer></v-spacer>
                            <v-btn color="error" class="mr-1" width="80" @click="deleteGroupDialog_2=true; deleteGroupDialog_1=false">DELETE</v-btn>
                            <v-btn  width="80"  @click="deleteGroupDialog_1 =false ">Cancel</v-btn>
                            <v-spacer></v-spacer>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="deleteGroupDialog_2" max-width="500px"  overlay-opacity=".9" persistent>
                    <v-card outlined dark class="deleteDialog_2" min-height="375">
                        <v-card-title  class="headline">Delete Group - {{selectedTabName}}</v-card-title>
                        <v-card-text class="pt-2 body-1">There is no coming back from this. Are you sure? If you still want to do this then enter the delete code below..</v-card-text>
                        <v-card-text class="pt-2 body-1">{{deleteCode}}</v-card-text>
                        <v-card-text class="pt-2 body-1"><v-text-field outlined dense v-model="deleteCodeText" label="Delete Code"></v-text-field></v-card-text>
                        <v-spacer></v-spacer>
                        <v-card-actions class="pb-4">
                            <v-spacer></v-spacer>
                            <v-btn color="error" :disabled="deleteConfirmed" class="mr-1" width="80" @click="deleteGroup">DELETE</v-btn>
                            <v-btn  width="80"  @click="deleteGroupDialog_2 = false">Cancel</v-btn>
                            <v-spacer></v-spacer>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
        </div>
      </v-tab-item>
    </v-tabs> 
   <!-- </v-card> 
   <v-card class="mt-10 px-2" >
     <v-card-title class="mx-n2">EVENTS</v-card-title>
     <v-card outlined v-for="(group, key) in groups" :key="key" >
     <v-expansion-panels focusable :accordion="$vuetify.breakpoint.mobile" :inset="!$vuetify.breakpoint.mobile" >
      <v-expansion-panel >
              <v-expansion-panel-header class="text-uppercase">{{group}}</v-expansion-panel-header>
              <v-expansion-panel-content>
          <v-card-text :class="['', { 'isMobile' : $vuetify.breakpoint.mobile }]">
           
            <v-tabs horizontal  show-arrows  :id="group">
              <v-tab v-for="(type, key) in eventTypes" :key="key" >{{type}}</v-tab>
                
                <v-tab-item v-for="(group, key) in groups" :key="key">
                  <EventsTable :activeTab="group" :tableTitle="eventTypes[key]" class="pa-0"/>
                  
                </v-tab-item>
              
            </v-tabs>
          </v-card-text>
            </v-expansion-panel-content>
      </v-expansion-panel>
      </v-expansion-panels>
     </v-card> -->
   </v-card> 
   <InfoD v-model="showInfo" v-if='showInfo' />
   <!-- <ReadingsDetails v-if="showInfo" v-model="showInfo" :id="'XL71070'"/> -->
  </div>
</template>

<script>
  import EventsTable from '../components/EventsTable'
  import InfoD from '@/components/InfoD'
  export default {
    name:"EventsView",
    components:{
      EventsTable,
      InfoD
    },
    data: () => ({
      addGroupWarning:'',
      addGroupDialog:false,
      addGroupTextBox:'',
      eventTypes:['Email','Notification','Log'],
        groups:[],
        selectedTab:'',
        selectedTabName:'',
        showInfo:false,
        deleteGroupDialog_1:false,
        deleteGroupDialog_2:false,
        deleteCode:'',
        deleteCodeText:''
    }),
   

    created () {

      /* let vm = this
      this.$http.get('/api/events/groups/',function(err,ret){
        vm.groups = ret.data.results
       // console.log(vm.groups)
      }) */
      this.$http.get('/api/events/groups').then(res => {
        this.groups = res.results
      })
      this.getDeleteCode()
    },

    methods: {
      getDeleteCode(){
        var result = ''
        var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
        var charactersLength = characters.length
        for ( var i = 0; i < 5; i++ ) {
            result += characters.charAt(Math.floor(Math.random() * charactersLength))
        }
        this.deleteCode = result
      },
      deleteGroup(){
        let vm = this
        this.deleteCodeText = ''
        let group = {name:this.selectedTabName}
        this.$http.post('/api/events/removeByGroup',group,function(err,ret){
                console.log(err || ret)
                 if(!err){
                    //vm.$delete(vm.groups,vm.selectedTabName)
                    vm.groups = vm.groups.filter(a => a !== vm.selectedTabName)
                    vm.deleteGroupDialog_2 = false  
                } 
                
            })
      },
      addGroup(){
        let v = this.$refs.addGroup.validate()
        if(v){
          this.addGroupTextBox = this.addGroupTextBox.toUpperCase()
          if(!this.groups.includes(this.addGroupTextBox)){
            this.groups.push(this.addGroupTextBox)
            this.addGroupDialog = false
            this.addGroupTextBox = ''
            this.selectedTab = this.groups.length - 1
            this.addGroupWarning = ''
          } else {
            this.addGroupWarning = 'Duplicate group name.'
          }
        }
        
      },
      cancelAddGroup(){
        this.addGroupDialog = false
        this.addGroupTextBox = ''
        this.addGroupWarning = ''
      }
    },
    watch:{
      selectedTab: function(){
        this.selectedTabName = this.groups[this.selectedTab]
        //console.log(this.selectedTabName)
      }
    },
    computed:{
      isDefultGroup(){
        return this.selectedTabName === 'default'
      },
      deleteConfirmed(){
        if(this.deleteCode === this.deleteCodeText){
          return false
        } else {
          return true
        }
      }
    }
  }
</script>
<style scoped>
.isMobile{padding: 0 !important;}
.v-expansion-panel-content >>> .v-expansion-panel-content__wrap { padding: 0 !important; }
.redOverlay{
  color: rgba(255, 59, 59, 0.7);
}
.deleteDialog_2{
  border: 3px solid #EE2D3F !important
}

</style>