<template>
<div class="eventsTable">
<v-card outlined elevation="16">
    <v-data-table :headers="headers" :items="desserts" :items-per-page="5"  dense>
        <template v-slot:top>
            <v-toolbar flat>
                <v-toolbar-title>{{tableTitle}}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-btn color="primary" dark class="mb-2 mx-2" v-show="showSubscribers" v-if="!$vuetify.breakpoint.mobile" @click="showSubscribersDialog=true" >Subscribers</v-btn>
                <v-btn color="primary" dark class="mb-2 mx-2" :disabled="disableAddEvents" v-if="!$vuetify.breakpoint.mobile" @click="addEvent">Add Event</v-btn> 
                <v-icon class="mobileMenu" v-if="$vuetify.breakpoint.mobile" @click="eventTableMobileDialog = true">{{$icon.mdiDotsVertical}}</v-icon> 
 <!-- ************************** menu dialog  REMOVE ME MAYBE I DONT THINK THIS IS BEING USED *************************************** -->               
                <v-dialog v-model="editDialog" max-width="400px" overlay-opacity=".8">
                    <v-card elevation="16" outlined>
                        <v-card-title class="headline justify-center">{{tableTitle}} Events Menu</v-card-title>
                        <v-card-actions>
                            <v-spacer></v-spacer>                           
                            <v-btn color="primary" dark class="" v-show="showSubscribers" @click="showSubscribersDialog=true"  >Subscribers</v-btn>
                            <v-btn color="primary" dark class="" :disabled="disableAddEvents" @click="dialog=true" >Add Event</v-btn>
                            <v-spacer></v-spacer>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
 <!-- ***************************************************************************** --> 

 <!-- **************************** Delete Dialog ********************************** -->
                <v-dialog v-model="dialogDelete" max-width="300px" overlay-opacity=".9" persistent >
                    <v-card outlined dark class="deleteDialog">
                        <v-card-title  class="headline " >Delete Event</v-card-title>
                        <v-card-subtitle class="pt-2">This will be permanently deleted.</v-card-subtitle>
                        <v-card-actions class="pb-4">
                            <v-spacer></v-spacer>
                            <v-btn color="error" class="mr-1" width="80" @click="deleteItemConfirm">DELETE</v-btn>
                            <v-btn  width="80"  @click="closeDelete">Cancel</v-btn>
                            <v-spacer></v-spacer>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
 <!-- ***************************************************************************** -->               
                <v-dialog v-model="eventTableMobileDialog" max-width="400px" overlay-opacity=".8">
                    <v-card elevation="16" outlined >
                        <v-card-title class="headline justify-center">{{tableTitle}} Events Menu</v-card-title>
                        <v-card-actions>
                            <v-spacer></v-spacer>                           
                            <v-btn color="primary" dark class="" v-show="showSubscribers" @click="showSubscribersDialog=true ; eventTableMobileDialog=false">Subscribers</v-btn>
                            <v-btn color="primary" dark class="" :disabled="disableAddEvents" @click="addEvent(); eventTableMobileDialog=false">Add Event</v-btn>
                            <v-spacer></v-spacer>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
             <v-tooltip v-if="!dialog" bottom slot="append" color=""><template v-slot:activator="{on}"><v-icon v-on="on" size="20" class="mr-0" @click="editItem(item)">{{$icon.mdiPencil}}</v-icon></template><span>Edit</span></v-tooltip>
             <v-tooltip v-if="!dialogDelete" bottom slot="append" color=""><template v-slot:activator="{on}"><v-icon v-on="on" size="20" class="mx-1" :disabled="activeTab === 'default'" @click="deleteItem(item)">{{$icon.mdiDelete}}</v-icon></template><span>Delete</span></v-tooltip>
        </template>
        <template v-slot:no-data>
            <span>No Events</span>
        </template>
        
    </v-data-table>
    
</v-card>
<EventsEditDialog v-model="dialog" v-if="dialog"  :mode='mode' :events="editTest" :groupName="activeTab" :eventType="tableTitle.toLowerCase()"/>
<Email v-model="showSubscribersDialog" :groupName="activeTab" />
</div>
</template>

