            const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            return pattern.test(value) || 'Invalid e-mail.'
          },
        },
        emailHeaders:[
            {
                text:'Active',
                align:'start',
                value:'active',
                sortable: false
            },
            {
                text:'Email',
                value:'email',
                sortable: false
            },
            {
                text:'Actions',
                value:'actions',
                sortable: false
            }
        ],
