            {
                active:false,
                email:'rjones@nepgroup.com',
                actions:'test/remove'
            },
            {
                active:true,
                email:'test@nepgroup.com',
                actions:'test/remove'
            }
        ],
        showSubscribersDialog:false,
        eventTableMobileDialog:false,
        showSubscribers: false,
        disableAddEvents:false,
        groups:[],
        registerId: ['XL71001', 'XL71002', 'XL71003', 'XL71004','XL71068_3'],
        comparisonTypes: ['equal', 'notEqual', 'lessThan', 'lessThanOrEqual', 'graterThan', 'gratherThanOrEqual', 'between', 'notBetween'],
        optionTypes: ['XL71001', 'XL71002', 'XL71003', 'XL71004'],
      editDialog: false,
      dialogDelete: false,
      headers: [
