        { text: 'Comparison', value: 'event.comparison' },
        { text: 'Value', value: 'event.value' },
        { text: 'Buffer (min)', value: 'event.options.buffer' },
        { text: 'One Shot', value: 'event.options.oneShot' },
        { text: 'Suppress (hr)', value: 'event.options.suppress' },
        { text: 'Last Event', value: 'event.lastEvent' },
        { text: 'Count', value: 'event.eventCount', },
        { text: 'Actions', value: 'actions', sortable: false },
        
