        register_id: '',
        event:{
            comparison:['equal', 'notEqual', 'lessThan', 'lessThanOrEqual', 'graterThan', 'gratherThanOrEqual', 'between', 'notBetween'],
            value:'',
            lastEvent: 0,
            eventCount: 0,
            options:{
                buffer:'',
                oneShot:'',
                suppress:'',
            }
        }
      }, */
