        event:{
            comparison:['equal', 'notEqual', 'lessThan', 'lessThanOrEqual', 'graterThan', 'gratherThanOrEqual', 'between', 'notBetween'],
            value:'',
            lastEvent: 0,
            eventCount: 0,
            options:{
                buffer:'',
                oneShot:'',
                suppress:'',
            }
        }
      },
      i:0
    }),
   
    computed: {
      formTitle () {
          console.log("computed formTitle")
        return this.editedIndex === -1 ? 'New Event' : 'Edit Event'
      },
    },

    watch: {
        
        
      /* dialog (val) {
        val || this.close()
      }, */
      dialogDelete (val) {
        val || this.closeDelete()
      },
      activeTab () {
         // let vm = this
          //console.log(this.activeTab)
        if(this.activeTab === 'default'){
            this.disableAddEvents = true
        } else {
            this.disableAddEvents = false
        }
        /* this.$http.get('/events/get/'+vm.activeTab,function(err,ret){
            console.log(ret.data.results)
            vm.desserts = ret.data.results[vm.tableTitle.toLowerCase()]
        }) */


      },
     /*  editedItem: function(){
          console.log("watch =>",this.editedItem)
      } */

    },

