        eventBus.$on('eventDialog',this.save)
        //this.registerId = this.$viewTest
        //let vm = this
      //this.initialize()

    /* this.$http.get('/api/events/get/'+this.activeTab,function(err,ret){
        if(Object.prototype.hasOwnProperty.call(ret.data.results, vm.tableTitle.toLowerCase())){
            vm.desserts = ret.data.results[vm.tableTitle.toLowerCase()]
        }
        vm.desserts.forEach(function (x,i) {
            if(x.event.lastEvent > 0){
                const milliseconds = x.event.lastEvent
                const dateObject = new Date(milliseconds)
                const humanDate = dateObject.toLocaleString()
                vm.desserts[i].event.lastEvent = humanDate
            }
        })
    })  */
    this.$http.get(`/api/events/get/${this.activeTab}`).then(res => {
        if(Object.prototype.hasOwnProperty.call(res.results, this.tableTitle.toLowerCase())){
            this.desserts = res.results[this.tableTitle.toLowerCase()]
        }
        this.desserts.forEach(function (x,i) {
            if(x.event.lastEvent > 0){
                const milliseconds = x.event.lastEvent
                const dateObject = new Date(milliseconds)
                const humanDate = dateObject.toLocaleString()
                this.desserts[i].event.lastEvent = humanDate
            }
        })
    })
    /* this.$http.get('/events/groups/',function(err,ret){
        vm.groups.push(ret.data.results)
    }) */
   // console.log(this.groups)
   if(this.activeTab === 'default'){
       this.disableAddEvents = true
   } else {
       this.disableAddEvents = false
   }
   if(this.tableTitle === 'Email') this.showSubscribers = true
    },

    methods: {
        emitTest(){
           /*  const clean = (obj,keyToRemove) => {
                if(obj[keyToRemove] === null || obj[keyToRemove] === 'undefinded' || obj[keyToRemove] === ""){
                    delete obj[keyToRemove]
                }
            } */
            /* Object.keys(data).forEach(x => {
                if(data.hasOwnProperty('eventTag'))clean(data,'eventTag')
                Object.keys(x.event).forEach(y => {
                    Object.keys(y.).forEach(z => {
                    
                })
                })
            }) */
            this.i = +1
            /* if(data.event.options.buffer === "") delete data.event.options.buffer*/
            /* if(Object.prototype.hasOwnProperty.call(data,'eventTag'))clean(data,'eventTag')
            if(Object.prototype.hasOwnProperty.call(data.event.options,'buffer'))clean(data.event.options,'buffer')
            if(Object.prototype.hasOwnProperty.call(data.event.options,'oneShot'))clean(data.event.options,'oneShot')
            if(Object.prototype.hasOwnProperty.call(data.event.options,'suppress'))clean(data.event.options,'suppress')
             */
            /* Object.keys(data).forEach(x => {
                if(x === "") {
                    delete data[x]
                } else {
                    if(typeof x === 'object'){
                        O
                    }
                }
                
            }) */


            /* console.log('emited mode =>',mode)
            console.log(this.i,'emited data =>',data)

 */

        },
        
        validate (){
            console.log("validate", this.$refs.editForm.validate())
        },
      initialize () {
        this.desserts = [
         
        ] 
      },
        addEmail(){
            this.emailArray.push(this.email)
            this.email = ''       
        },
      editItem (item) {
        console.log("edit item =>",item)
        this.editedIndex = this.desserts.indexOf(item)
        console.log('here is the edit index =>',this.editedIndex)
        
        //console.log("edit item with indexof=>",this.desserts[this.desserts.indexOf(item)])
        //this.editedItem = Object.assign({}, item)
        this.editTest = Object.assign({}, item)

        this.mode = 'edit'
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        
        //let vm = this
        //console.log("LOOK HERE =====>",this.desserts[this.editedIndex].uuid)
        let IdToRemove = {uuid:this.desserts[this.editedIndex].uuid}
        /* this.$http.post('/api/events/removeById',IdToRemove,function(err,ret){
        console.log(err || ret)
            if(!err){
                vm.desserts.splice(vm.editedIndex, 1)
                vm.closeDelete()
            }
        }) */

        this.$http.post('/api/events/removeById',IdToRemove,).then(res => {
            this.desserts.splice(this.editedIndex, 1)
            this.closeDelete
            console.log(res)
        })


      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save (mode,data) {
          console.log(mode,data)
          //console.log("look here=>",this.desserts[this.editedIndex])
       if (this.editedIndex > -1) {
           console.log("save edit index=>",this.editedIndex)
          //Object.assign(this.desserts[this.editedIndex], this.editedItem)
          //this.desserts[this.editedIndex] = data
          this.$set(this.desserts,this.editedIndex,data)
          console.log("DESSERTS =>",this.desserts)
        } else {
          this.desserts.push(data)
       }
       //this.close()
        },
        addEvent(){
            this.mode = 'add'
            this.editedIndex = -1
            this.editTest = Object.assign({}, this.$emptyEvent)
            this.dialog = true
        },

    },
  }
</script>
<style scoped>
.mobileMenu{
    cursor: pointer;
}
.deleteDialog{
    background-color: black;
    border-color: #383838;
}

</style>