<template>
    <div class="Email">
        <v-dialog v-model="show" max-width="700px" overlay-opacity=".8" :fullscreen ="$vuetify.breakpoint.mobile" >
        <v-card elevation="16" outlined >
            <v-card-title class="headline px-3">{{$view.facility.name}} - {{groupName}} Group<v-spacer></v-spacer><v-btn icon @click="show=false"><v-icon>{{$icon.mdiClose}}</v-icon></v-btn></v-card-title>
            <v-card-subtitle class="pt-2">Email Alerts</v-card-subtitle>
            <v-card-text>Add your email below to receive email alerts from {{$view.facility.name}}</v-card-text>
            <div :class="['', { 'px-16' : !$vuetify.breakpoint.mobile }]"><v-form ref="email" lazy-validation><v-card-text ><v-text-field v-model="textEmail" :rules="$rules.email.concat($rules.required)"  label="E-mail"><v-btn icon slot="append" ><v-icon  style="cursor:pointer" @click="addEmail()">{{$icon.mdiPlus}}</v-icon></v-btn></v-text-field></v-card-text></v-form></div>
            <div class="d-flex justify-center mt-n4 mb-4"><span class="overline fontColor">{{msg}}</span></div>
                <v-divider></v-divider><v-subheader>Subscribers</v-subheader>
                <v-card-text >
                    <v-data-table class="elevation-16" :headers="emailHeaders" :items="emailAddresses" hide-default-footer :no-data-text="emailError">
                        <template v-slot:[`item.email`]="props">
                            <v-edit-dialog :return-value.sync="props.item.email">
                                {{ props.item.email }}
                                <template v-slot:input>
                                    <v-text-field v-model="props.item.email" label="Edit" single-line counter></v-text-field>
                                </template>
                            </v-edit-dialog>
                        </template>
                        <template v-slot:[`item.active`]="{ item }">
                            <v-simple-checkbox v-model="item.active" :ripple="false" @click="activeEmail(item)"></v-simple-checkbox>
                        </template>
                        <template v-slot:[`item.actions`]="{ item }">
                            <v-tooltip bottom slot="append" color=""><template v-slot:activator="{on}"><v-icon v-on="on" size="20" class="mr-2" @click="testEmail(item)">{{$icon.mdiEmailSend}}</v-icon></template><span>Send Test Email</span></v-tooltip>
                            <v-tooltip bottom slot="append" color=""><template v-slot:activator="{on}"><v-icon v-on="on"  size="20" class="mr-2" @click="deleteEmail(item)">{{$icon.mdiDelete}}</v-icon></template><span>Delete</span></v-tooltip>
                        </template>
                    </v-data-table>
                </v-card-text>
        </v-card>
    </v-dialog>
    </div>
</template>
<script>

export default {
    name:"emailDialog",
    props:['value','groupName'],
    data: () => ({
        msg:'',
        emailAddresses:[],
        emailError:'No data available',
        textEmail:'',
        emailHeaders:[
            {
                text:'Active',
                align:'start',
                value:'active',
                sortable: false
            },
            {
                text:'Email',
                value:'email',
                sortable: false
            },
            {
                text:'Actions',
                value:'actions',
                sortable: false
            }
        ],
    }),
    created(){
        //let vm = this
        /* this.$http.getEmail("/api/"+this.$view.facility.name+"/emailList/"+this.groupName,function(err,ret){
            if(!err){
                ret.data.forEach(x => {
                    x.active = (x.active ==='true')
                    vm.emailAddresses.push(x)
                })
            } else {
                vm.emailError = err
            }
        }) */
        this.$http.getEmail(`/api/${this.$view.facility.name}/emailList/${this.groupName}`).then(res => {
            res.forEach(x => {
                    x.active = (x.active ==='true')
                    this.emailAddresses.push(x)
                })
        })
    },
    methods:{
        activeEmail(x){
            const email = this.emailAddresses[this.emailAddresses.indexOf(x)].email
            this.emailData({task:x.active,group:this.groupName,email:email})
        },
        testEmail(x){
            const email = this.emailAddresses[this.emailAddresses.indexOf(x)].email
            this.emailData({task:'test',group:this.groupName,email:email})
        },
        deleteEmail(x){
            const email = this.emailAddresses[this.emailAddresses.indexOf(x)].email
            this.$delete(this.emailAddresses,this.emailAddresses.indexOf(x))
            this.emailData({task:'remove',group:this.groupName,email:email})
        },
        addEmail(){
            let v = this.$refs.email.validate()
            if(v){
                if(!this.emailAddresses.find(x => x.email === this.textEmail)){
                    const obj = {active:true,email:this.textEmail}
                    this.$refs.email.reset()
                    this.textEmail = null
                    this.emailAddresses.push(obj)
                    this.emailData({task:'add',group:this.groupName,email:obj.email})
                } else {
                this.sendMSG("This address already exist!")
                }
                
            }
        },
        emailData(x){
            /* let vm = this
            this.$http.postEmail("/api/"+this.$view.facility.name+"/email",x,function(err,ret){
                if(!err){
                    vm.sendMSG(ret.data) 
                } 
            }) */
            this.$http.getEmail(`/api/${this.$view.facility.name}/email`,x).then(res => {
                this.sendMSG(res)
            })

        },
        sendMSG(x){
            this.msg = x
            setTimeout(() => {
                this.msg = ''
            }, 10000);
        }
    },
    computed:{
        show: {
            get () {
                return this.value
            },
            set (value) {
                this.$emit('input', value)
            } 
        },
    }
}
</script>
<style scoped>
   .fontColor{
       color: #FFCF06 !important
   }
</style>